@import "../../../styles/variables.scss";

.container {
	width: 100vw;
	height: 100vh;
	display: flex;

	.imgcontainer {
		background-color: $primary;
		width: 100%;
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		justify-content: flex-end;
		overflow: hidden;

		.btncontainer {
			align-items: flex-end;
			margin: auto 3.5rem 1.5rem 4rem;
			z-index: 100;

			& > * {
				// color: $blue;
				letter-spacing: -2.5px;
			}
			// &:hover {
			// 	background-color: rgba($color: $blue, $alpha: 0.2);
			// }
		}

		&::before {
			content: '';
			background-image: url('../../../media/woman.png');
			background-size: cover;
			background-repeat: no-repeat;
			position: absolute;
			right: -5%;
			height: 100%;
			width: 100%;
			z-index: 2;
		}
	}

	.logincontainer {
		max-width: 25%;
		padding:3rem;
		border-radius: 0;
		display: flex;
		flex-direction: column;
		text-align: center;
		justify-content: center;
		justify-items: center;
		background-color: $blue;
		position: relative;
		z-index: 100;

		// &::before {
		// 	content: '';
		// 	background-image: url('../../../media/waves.png');
		// 	background-size: contain;
    //   background-repeat: no-repeat;
    //   position: absolute;
    //   left: 65%;
    //   height: 100%;
    //   width: 100%;
    //   z-index: -1;
		// }
	}

	.logocontainer {
		width: 60%;
		align-self: center;

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}

	.linkcontainer {
		margin-top: auto;
		.linkpass {
			margin-bottom: 1rem;
			&:hover {
				background-color: rgba($color: $primary, $alpha: 0.2);
				color: $primary;
			}
		}
	}
}
