@import "../styles/variables.scss";

.container {
	background-color: rgba($color: $blue, $alpha: 0.3);

	width: 100vw;
	min-height: 100vh;
	position: relative;
	overflow: hidden;

	&::before {
		content: '';
		background-image: url("../media/bg-drop.png");
		position: absolute;
		height: 100%;
		width: 120%;
		background-size: contain;
		left: -15%;
		opacity: 0.2;
	}

	.cardContainer {
		display:flex;
		gap: 1.5rem;
		flex-wrap: wrap;
		align-items: center;
		justify-content: center;
		padding: 5rem;
		z-index: 100;
		position: relative;
	}
}
